import React, { useEffect, useRef, useState } from 'react';
import './MapRenderer.css';

const MAX_RETRIES = 3;
const RETRY_DELAY = 1000;

const MapRenderer = ({ diagram }) => {
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [retryCount, setRetryCount] = useState(0);
  const elementRef = useRef(null);
  const renderRef = useRef(null);
  const mermaidRef = useRef(null);

  const initializeMermaid = async () => {
    if (!mermaidRef.current) {
      const { default: mermaid } = await import('mermaid');
      mermaid.initialize({
        startOnLoad: false,
        theme: 'default',
        securityLevel: 'loose',
        flowchart: {
          useMaxWidth: true,
          htmlLabels: true,
        },
        fontSize: 16,
        fontFamily: 'Arial',
        maxTextSize: 50000
      });
      mermaidRef.current = mermaid;
    }
    return mermaidRef.current;
  };

  const renderDiagram = async (retryAttempt = 0) => {
    if (!diagram || !elementRef.current) return;

    try {
      setIsLoading(true);
      setError(null);

      // Clear previous content
      elementRef.current.innerHTML = '';

      // Create container for the diagram
      const container = document.createElement('pre');
      container.className = 'mermaid';
      container.textContent = diagram;
      elementRef.current.appendChild(container);

      // Initialize mermaid if not already initialized
      const mermaid = await initializeMermaid();

      // Cancel any previous render attempts
      if (renderRef.current) {
        clearTimeout(renderRef.current);
      }

      // Add a slight delay to ensure the DOM is ready
      renderRef.current = setTimeout(async () => {
        try {
          // Parse and render
          await mermaid.parse(diagram);
          await mermaid.run({
            nodes: [container],
            suppressErrors: true
          });

          setIsLoading(false);
          setRetryCount(0); // Reset retry count on success
        } catch (err) {
          console.error(`Mermaid render error (attempt ${retryAttempt + 1}):`, err);
          
          if (retryAttempt < MAX_RETRIES) {
            // Attempt retry after delay
            setTimeout(() => {
              setRetryCount(retryAttempt + 1);
              renderDiagram(retryAttempt + 1);
            }, RETRY_DELAY);
          } else {
            setError(`Failed to render diagram after ${MAX_RETRIES} attempts. ${err.message || ''}`);
            setIsLoading(false);
          }
        }
      }, 100);
    } catch (err) {
      console.error('Mermaid load error:', err);
      setError(`Failed to load Mermaid: ${err.message || 'Unknown error'}`);
      setIsLoading(false);
    }
  };

  useEffect(() => {
    let mounted = true;

    const cleanup = () => {
      mounted = false;
      if (renderRef.current) {
        clearTimeout(renderRef.current);
      }
    };

    if (mounted) {
      renderDiagram(retryCount);
    }

    return cleanup;
  }, [diagram, retryCount]);

  const handleRetry = () => {
    setRetryCount(0);
    renderDiagram(0);
  };

  return (
    <div className="diagram-wrapper">
      {isLoading && (
        <div className="loading-overlay">
          <div className="loading-spinner"></div>
          <p>Loading diagram{retryCount > 0 ? ` (Attempt ${retryCount + 1}/${MAX_RETRIES})` : ''}...</p>
        </div>
      )}
      {error && (
        <div className="error-message">
          <p>{error}</p>
          <button onClick={handleRetry}>
            Retry
          </button>
        </div>
      )}
      <div ref={elementRef} className="diagram-container"></div>
    </div>
  );
};

export default MapRenderer;
